@import 'variables.scss';

$heading-height: 45px;

.handle {
    display: none!important;
}

.addressBook {
    height: 60px !important;
    background-color: #171826!important;
    border: none!important;
    border-radius: 0 12px 12px 0!important;
    padding: 0 10px;
    opacity: 1;
    transition: .3s ease-in-out opacity;

    &:hover {
        cursor: pointer;
    }

    .content {
        cursor: pointer;

        .title {
            flex-direction: row !important;
        }
    }

    :global(.menu) {
        top: 60px !important;
        display: flex;
        flex-direction: column;
        gap: 0;
        padding: 0;
        white-space: nowrap;

        .button {
            height: 100%;
            @include flex-center-center;
            font-size: 17px;
            font-weight: bold;
            color: white;

            &:not([disabled]):hover {
                cursor: pointer;
                opacity: 1;
                background-color: rgba(#1E2033, 0.2) !important;
            }

            &:active {
                background-color: #1E2033!important;
            }
        }

        .heading {
            height: $heading-height;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-left: 20px;
            background-color: #1E2033;
            
            .title {
                display: flex;
                flex-direction: row !important;
                align-items: center;
                gap: 10px;
                font-size: 15px;
                font-weight: bold;
            }

            .button {
                width: $heading-height;
            }
        }

        .content {
            max-height: 250px;
            display: flex;
            flex-direction: column;
            min-width: 300px;
            padding: 0;
            overflow-y: auto;

            .text-input {
                input {
                    padding-right: initial !important;
                }
            }
        }

        .addAddress {
            padding: 10px;

            .fields {
                display: flex;
                flex-direction: column;
                gap: 10px;

                input[type=text] {
                    height: 40px;
                    width: 300px;
                    font-size: 1em !important;
                    background-color: #1E2033;
                    border-radius: 8px;
                    color: #ebecff;
                }
            }

            button {
                width: 100%;
                color: #ebecff;
            }
        }
    }
}

// Mobile
@media screen and (max-width: $mobile-breakpoint-max-width) {
    .addressBook {
        .menu {
            width: 100%;
        }
    }
}